import React, { useState } from 'react';
import ModelBox from '../HomeComponents/ModelBox';
import Modal from '@mui/material/Modal';
import { Link } from 'react-router-dom';
import jsonData from "../data.json";

const AboutUs = (props) => {
  let items = jsonData["aboutUsModel"]; 
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <>
    <section className="about py-lg-5 py-3" id="about">
      <div className="container">
        <div className="inner-sec-w3ls-agileits py-lg-5 py-3">
          <div className="header-info about-in mt-5 pb-5 text-center">
            <h3 className="heading">About our studio  </h3>
            

            <p className="para mt-4"> Ipsum is simply dummy text of the printing and typesetting industry.Lorem Ipsum is simply dummy text of the printing
              and typesetting industry. et sed libero. empor mauris tincidunt Nunc Nam vel feugiat, Lorem Ipsum is simply dummy text
              of the printing and typesetting industry</p>
            {/* <a  data-toggle="modal" data-target="#exampleModalCenter" className="btn btn-sm animated-button gibson-three mt-4"></a> */}
            <Link 
              className="btn btn-sm animated-button gibson-three mt-4" 
              type="button" onClick={handleOpen}>
              Read More
            </Link>
            <Modal open={open} onClose={handleClose} jsonitems={items} >
              <>
                  <ModelBox handleClose={handleClose} jsonitems={items} />
              </>
            </Modal>

          </div>
        </div>
      </div>
	  </section>
    </>
  )
}

export default AboutUs;