import React from 'react';
import Img from './../Home/images/g1.jpg';
const ModelBox = (props) => {


  return (
    <>
      <div className="modal-dialog modal-dialog-centered" role="document">
			<div className="modal-content">
				<div className="modal-header text-center">
					<h5 className="modal-title text-center" id="exampleModalLongTitle">{props.jsonitems.title}</h5>
					<button type="button" className="close" data-dismiss="modal" aria-label="Close" onClick={props.handleClose}>
						<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div className="modal-body">
					<img src={Img} className="img-fluid" alt={Img}/>
					<h4>{props.jsonitems.subTitle}</h4>
					<p>{props.jsonitems.description}</p>
				</div>
				<div className="modal-footer">
					<button type="button" className="btn btn-secondary" data-dismiss="modal" onClick={props.handleClose}>Close</button>
					{/* <button type="button" className="btn btn-primary">Save changes</button> */}
				</div>
			</div>
		</div>
    </>
  )
}

export default ModelBox;