import React from 'react';
import "./css/bootstrap.css";
import "./css/style.css";
// import "./css/popuo-box.css";
import { Link } from 'react-scroll';

const Header = () => {
  return (
    <>
    {/* <head>
     <link href="css/bootstrap.css" rel='stylesheet' type='text/css' /> */}
     {/* </head> */}
    <section className="banner-sec-w3layouts" id="home">
    <nav className="navbar navbar-expand-lg navbar-light bg-light top-header">
        <div className="logo text-center">
            <h1 className="logo">
                <a className="navbar-brand" href="/">
                    <span className="sub"></span>Vk</a>
            </h1>
        </div>
        <button className="navbar-toggler mx-auto" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
            aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon">
                <i className="fas fa-bars"></i>
            </span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav mx-auto">
                <li className="nav-item active">
                    <Link className="nav-link ml-lg-0" to="/">Home
                        <span className="sr-only">(current)</span>
                    </Link>
                </li>
                <li className="nav-item">
                    <Link  
                        className='nav-link scroll' 
                        activeclass="active" 
                        to="about" 
                        spy={true} 
                        smooth={true} 
                        duration={500}>
                        About
                    </Link>
                </li>
                <li className="nav-item">
                    <Link  
                        className='nav-link scroll' 
                        activeclass="active" 
                        to="services" 
                        spy={true} 
                        smooth={true} 
                        duration={500}>
                        Services
                    </Link>
                </li>
                <li className="nav-item dropdown">
                    <Link className="nav-link dropdown-toggle" to="/" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                        aria-expanded="false">
                        Dropdown
                    </Link>
                    <div className="dropdown-menu" aria-labelledby="navbarDropdown">

                        <a className="dropdown-item text-center scroll" href="#team">Team</a>
                        <a className="dropdown-item text-center scroll" href="#gallery">Photo Gallery</a>
                        <a className="dropdown-item text-center scroll" href="#testimonials">Testimonials</a>

                    </div>
                </li>
                <li className="nav-item">
                    <a className="nav-link scroll" href="#contact">Contact</a>
                </li>
            </ul>
        </div>
        <div className="phone-inline my-2 my-lg-0">

            <h6>
                <span className="fas fa-phone"></span> +91 85739 97939</h6>
        </div>
    </nav>
    </section>
    </>
  )
}

export default Header